import React , {useState, useEffect} from 'react';
import './Detail.scss';
import Data from './Item.js';
import { useHistory, useParams } from 'react-router-dom';
import {Nav,Button} from 'react-bootstrap';
import {CSSTransition} from 'react-transition-group';

function Detail(props) {
    let history = useHistory();
    let {id} = useParams();
    let [tabNum, tabNumChange] = useState(0);
    let [inputVal, inputValChange] = useState('');
    let [opaSwitch, opaSwitchChange] = useState(false);

    let paramId = props.item.find((a)=>{
        return a.id == id;
    })

    let [paramReview,paramReviewChange] = useState([...paramId.review])
    // console.log(paramReview)


    let [likes, likesChange] = useState([...props.item]);
    // console.log(likes)


    return (
        <div className="detailContainer">
            <div className="detailIMG">
                <img src={`/img/data${id}.jpg`}></img>
            </div>
            <div className="detailTitle">
                <h3>{paramId.title}</h3>
                <div className="price">
                    <span className="moto">{paramId.price}원</span>
                    <span className="sale">{paramId.price - paramId.price * 20 / 100}원</span>
                </div>
                <div>
                {
                paramId.heart == false ? <span>🤍</span> : <span>🧡</span>
                } : {paramId.likes}</div>
                <button className="btn btn-danger"
                onClick = {()=>{history.push('/')}}>뒤로가기</button> 
                <button className="btn btn-danger" onClick={()=>{
                    paramId.heart == false ? paramId.heart = true : paramId.heart = false;
                    // console.log(paramId.heart)
                    paramId.heart == false ? likesChange(paramId.likes -= 1):likesChange(paramId.likes += 1)
                }}>찜하기</button> 
            </div>
            <div className="orderFixed">
            바로 구매하기
            </div>
            <Nav fill variant="tabs" defaultActiveKey="link-0">
                <Nav.Item>
                    <Nav.Link eventKey="link-0" onClick={()=>{
                        tabNumChange(0); opaSwitchChange(false)
                    }}>제품 상세</Nav.Link>
                </Nav.Item>
                <Nav.Item>
                    <Nav.Link eventKey="link-1" onClick={()=>{
                        tabNumChange(1); opaSwitchChange(false)
                    }}>리뷰</Nav.Link>
                </Nav.Item>
                <Nav.Item>
                    <Nav.Link eventKey="link-2">안내</Nav.Link>
                </Nav.Item>
                <Nav.Item>
                    <Nav.Link eventKey="link-3">
                    문의사항
                    </Nav.Link>
                </Nav.Item>
            </Nav>
            <CSSTransition in={opaSwitch} classNames="darara" timeout={500}>
            <TabCompo tabNum={tabNum} tabNumChange={tabNumChange}  paramId={paramId}
            inputValChange={inputValChange} inputVal={inputVal}
            paramReview={paramReview} paramReviewChange={paramReviewChange}
            opaSwitchChange={opaSwitchChange}/>
            </CSSTransition>
      </div>
    )
}

function TabCompo(props) {
    useEffect(()=> {
        props.opaSwitchChange(true);
    })
    if (props.tabNum == 0) {
        return (
            <div className="content">
                <div className = "contentTop">
                이 상품의 이름은 '{props.paramId.title}' 입니다. <br></br>
                가격은 {props.paramId.price}원입니다.<br></br>
                20% 할인행사중이니 현재가격 '{props.paramId.price - props.paramId.price * 20 / 100}원'입니다.<br></br>
                총 {props.paramId.likes}명이 이 상품을 찜했습니다.
                </div>
                <div className="blabla">
                우리 귀여운 {props.paramId.korName} 좀 보고 가세요!<br></br>
                {props.paramId.korName}! 너와 함께라면 무엇이든 할 수 있어!
                </div>
                <div className="contentIMG">
                    <img src={`/img/${props.paramId.char}1detail.jpg`}></img>
                </div>
                <div className="blabla">
                스트레스가 쌓일때는 {props.paramId.korName}친구를 안아주세요!<br></br>
                {props.paramId.korName}! 지금 안사면 후회할걸?!
                <div className="contentIMG">
                    <img src={`/img/${props.paramId.char}2detail.jpg`}></img>
                </div>
                <div className="contentIMG">
                    <img src={`/img/${props.paramId.char}3detail.jpg`}></img>
                </div>
                </div>
            </div>
        )
    }
    if (props.tabNum == 1) {
        return (
            <div>
            <ul className="review">
            {
             props.paramId.review.map(function(a,i) {
                return (
                <li>
                    <span>" {props.paramId.review[i]} "</span>
                    <div>2021.08.02</div>
                </li>
                )
            })
}
            </ul>
            <div className="reviewWrite">
                <input onChange={(e)=>{props.inputValChange(e.target.value)}}/>
                <Button variant="warning" onClick={()=>{
                 props.paramId.review.unshift(props.inputVal);
                }}>리뷰등록하기</Button>
            </div> 

            </div>
        )
    }
}

export default Detail;